a {
    text-decoration: none;
}

main {
    min-height: 100vh;
    position: relative;
}

main:before {
    content: "";
    background-image: linear-gradient(#fff, transparent);
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0.1;
}

.page {
    padding-top: 5em;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    max-width: 720px;
    text-align: center;
}
body{
    background: #eee;
    font-family: 'Nunito SemiBold';
}

#main h1{
    color: #444;
    text-align: center;
}

#main{
    padding: 0px;
    box-sizing: border-box;
    width: 60%;
    height: 100%;
}

#book-list{
    padding: 0;
}

#book-list li{
    display: inline-block;
    margin: 12px;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #880E4F;
    box-shadow: 1px 2px 3px rgba(0,0,0,0.3);
    cursor: pointer;
    color: #880E4F;
}

form{
    background: #fff;
    padding: 20px;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 400px;
}

form .field{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
}

form label{
    text-align: right;
    padding: 6px;
}

form input, form select{
    margin: 4px 0;
    padding: 6px;
    box-sizing: border-box;
}

form button{
    color: #fff;
    font-size: 2em;
    background: #AD1457;
    border: 0;
    padding: 0 10px;
    border-radius: 50%;
    cursor: pointer;
    position: absolute;
    bottom: 10px;
    left: 10px;
}
